import { useTranslation } from "react-i18next";
import style from "../FinanceManager.module.css";
import { useEffect, useState } from "react";
import UserMemberContainerWrapper from "../../../Ui/UserMember/UserMemberContainerWrapper";
import UserMemberContentWrapper from "../../../Ui/UserMember/UserMemberContentWrapper";
import BorderBottom from "../../../Ui/Border/BorderBottom";
import QvipleLoader from "../../../Loader/QvipleLoader";
import CustomSelect from "../../../Ui/Select/CustomSelect";
import CreateButton from "../../../Ui/Button/CreateButton";
import { useFinanceShowReceiptType } from "../../../Hooks/ApiHooks/FinanceManager/finance-manager-api";

const FinanceSetting = ({ masterData, fid }) => {
  const { t } = useTranslation();
  const [disabled, setDisabled] = useState(false);
  const [state, setState] = useState({
    show_receipt: "Normal",
  });

  const [financeShowReceiptType] = useFinanceShowReceiptType();

  useEffect(() => {
    if (masterData?.show_receipt) {
      setState((prev) => ({
        ...prev,
        show_receipt: masterData?.show_receipt,
      }));
    }
  }, [masterData?.show_receipt]);
  const onSelectDropdown = (e, flow) => {
    setState((prev) => ({
      ...prev,
      [flow]: e,
    }));
  };

  const onSave = () => {
    if (fid) {
      setDisabled((pre) => !pre);
      financeShowReceiptType({
        fid: fid,
        bodyParameter: state,
      })
        .then(() => {
          setDisabled((pre) => !pre);
        })
        .catch({});
    }
  };

  //   console.info("masterData", masterData);
  return (
    <UserMemberContainerWrapper>
      <UserMemberContentWrapper>
        <div className={style.fm_header_container}>
          <h6>{t("setting")}</h6>
        </div>
      </UserMemberContentWrapper>
      <BorderBottom />
      <UserMemberContentWrapper>
        <CustomSelect
          selectLabel={t("admission_receipt_type")}
          selectedValue={t("please_select_receipt_type")}
          defalutValue={state.show_receipt}
          onClick={(val) => onSelectDropdown(val, "show_receipt")}
          options={["Normal", "Society"]}
          customStyleContainer={{
            width: "49%",
          }}
          customListStyle={{
            position: "absolute",
            width: "100%",
            zIndex: "1",
          }}
        />
        <CreateButton label="save" onAction={onSave} />
      </UserMemberContentWrapper>
      {disabled && <QvipleLoader />}
    </UserMemberContainerWrapper>
  );
};

export default FinanceSetting;
